.App {
  text-align: center;
}
h5 {
  font-size: 1.25rem;
  margin-left: 10px;
  /* color: #de2721; */
  color: #00008b;
  /* color: rgba(15, 15, 15, 0.9); */
  font-weight: 500;
}
h6 {
  font-size: 1rem;
  margin-left: 10px;
  color: rgba(15, 15, 15, 0.9);
  font-weight: 500;
  margin-top: 5px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

.home,
.reports,
.products {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
