.navbar {
  background-color: #f8f8f8;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}
html,
body {
  height: 100%;
}
.nav-menu {
  /* background-color: #060b26; */
  background-color: #e34f4a;
  /* background-color: #00008b; */
  width: 250px;
  /* margin-top: 80px; */
  /* height: 400px; */
  display: flex;
  justify-content: center;
  /* overflow: auto; */
  overflow: auto;
  /* overflow-x: hidden; */
  height: 100%;
  position: fixed;
  /* padding-bottom: 100px; */
  /* top: 0; */
  /* left: -100%; */
  /* transition: 850ms; */
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 45px;
}

.anchor {
  text-decoration: none;
  color: #f0e8e8;
}

.nav-header {
  /* display: flex; */
  justify-content: start;
  align-items: center;
  color: #f0e8e8;
  padding: 8px 0px 8px 16px;
  list-style: none;
  /* height: 60px; */
}

.nav-text a {
  text-decoration: none;
  color: #f0e8e8;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
  font-size: 16px;
}

.container {
  display: flex;
  align-items: center;
}

.border {
  border-bottom: 1px solid black;
  width: 100%;
}

.content {
  padding: 0 10px 0 10px;
}
